.dg_grid_container {
  width: 100%;
  max-width: 1200px;
  margin-right: auto;
  margin-left: auto;
}

.dg_grid_row:after {
  content: '';
  display: table;
  clear: both;
}

.dg_grid_col {
  float: left;
  min-height: 1px;
  padding-left: 12px;
  padding-right: 12px;
  box-sizing: border-box;
}

@media (min-width: 641px) {
  .dg_grid_col {
    padding-left: 16px;
    padding-right: 16px;
  }
}

.dg_grid_col[class*="col_1"] {
  width: 8.33333333%;
}

.dg_grid_col[class*="col_2"] {
  width: 16.66666666%;
}

.dg_grid_col[class*="col_3"] {
  width: 24.99999999%;
}

.dg_grid_col[class*="col_4"] {
  width: 33.33333333%;
}

.dg_grid_col[class*="col_5"] {
  width: 41.66666666%;
}

.dg_grid_col[class*="col_6"] {
  width: 50%;
}

.dg_grid_col[class*="col_7"] {
  width: 58.33333333%;
}

.dg_grid_col[class*="col_8"] {
  width: 66.66666666%;
}

.dg_grid_col[class*="col_9"] {
  width: 74.99999999%;
}

.dg_grid_col[class*="col_10"] {
  width: 83.33333333%;
}

.dg_grid_col[class*="col_11"] {
  width: 91.66666666%;
}

.dg_grid_col[class*="col_12"] {
  width: 100%;
}

html,
body {
  height: 100%;
  background-color: #eeeded;
}

body {
  -webkit-font-smoothing: antialiased;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  color: #333333;
}

@media (min-width: 641px) {
  body {
    font-size: 19.2px;
  }
}

ol,
ul,
li,
a,
span {
  font-size: 16px;
}

@media (min-width: 641px) {
  ol,
  ul,
  li,
  a,
  span {
    font-size: 17.6px;
  }
}

p {
  font-size: 16px;
}

@media (min-width: 641px) {
  p {
    font-size: 17.6px;
  }
}

body,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Hind", sans-serif;
}

a {
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
  color: #333333;
  cursor: pointer;
  outline: none;
  text-decoration: none;
}

a:active, a:hover {
  color: #4d4d4d;
}

img {
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
}

.clearfix:before, footer.site-footer .container_sidebar_two .tagcloud:before,
footer.site-footer .container_sidebar_three .tagcloud:before,
.clearfix:after,
footer.site-footer .container_sidebar_two .tagcloud:after,
footer.site-footer .container_sidebar_three .tagcloud:after {
  content: " ";
  display: table;
}

.clearfix:after, footer.site-footer .container_sidebar_two .tagcloud:after,
footer.site-footer .container_sidebar_three .tagcloud:after {
  clear: both;
}

.button {
  text-decoration: none;
  display: inline-block;
  margin: 10px 0;
  padding: 10px 12px;
  border: 1px solid #ffffff;
  text-transform: uppercase;
  text-align: center;
  background-color: #ff8800;
  color: #ffffff;
  cursor: pointer;
  transition: background-color 0.4s ease;
}

.button:hover, .button:active, .button:focus {
  background-color: #000000;
  color: #ffffff;
}

.search-form {
  position: relative;
  width: 100%;
  padding: 12px 15px;
  box-sizing: border-box;
}

.search-form input {
  outline: none;
  width: 100%;
  height: 30px;
  padding: 4px 8px 4px 34px;
  font-size: 16px;
}

.search-form button[type="submit"] {
  outline: none;
  position: absolute;
  top: 12px;
  width: 30px;
  height: 30px;
  margin: 0;
  padding: 0;
}

.nav_crumb {
  margin-bottom: 20px;
  padding: 20px;
  border: 1px solid #e2e0e0;
  background-color: #ffffff;
}

.nav_crumb a,
.nav_crumb span {
  font-size: 12.8px;
  text-transform: uppercase;
}

.nav_crumb .current {
  color: #ff8800;
}

.container_page_nav {
  padding: 10px;
  border: 1px solid #e2e0e0;
  background-color: #ffffff;
}

.container_page_nav .page_number {
  display: inline-block;
  margin-right: 6px;
  padding: 6px 14px;
  background-color: #eeeded;
  transition: background-color 0.4s ease, color 0.4s ease;
}

.container_page_nav .page_number.current, .container_page_nav .page_number:hover {
  background-color: #ff8800;
  color: #ffffff;
}

.site {
  height: 100%;
  transition: transform 200ms ease-in;
}

.site.active {
  transform: translate3d(15.625rem, 0, 0);
}

@media (min-width: 641px) {
  .site {
    margin-left: 15.625rem;
  }
}

.container_global_menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 15.625rem;
  height: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #212121;
  transform: translate3d(-100%, 0, 0);
  transition: transform 200ms ease-in;
}

@media (min-width: 641px) {
  .container_global_menu {
    transform: translate3d(0, 0, 0);
  }
}

.container_global_menu ul {
  list-style: none;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.container_global_menu li {
  margin: 0;
}

.container_global_menu a {
  text-decoration: none;
  display: block;
  padding: 12px 15px;
  border-bottom: 1px solid #333333;
  font-size: 16px;
  color: #ffffff;
}

.container_global_menu a:hover {
  background: #080808;
}

.container_main_header {
  background-color: #212121;
  color: #ffffff;
}

.js-trigger-menu {
  width: 30px;
  height: 30px;
  background: red;
}

@media (min-width: 641px) {
  .js-trigger-menu {
    display: none;
  }
}

.logo_site {
  display: block;
  text-align: center;
}

footer.site-footer .container_sidebar_two,
footer.site-footer .container_sidebar_three {
  margin-bottom: 20px;
}

footer.site-footer .container_sidebar_two h2,
footer.site-footer .container_sidebar_three h2 {
  font-size: 16px;
  text-transform: uppercase;
}

footer.site-footer .container_sidebar_two ul,
footer.site-footer .container_sidebar_three ul {
  list-style-type: square;
  margin: 0 0 0 20px;
  padding: 10px 0 0 0;
}

footer.site-footer .container_sidebar_two li,
footer.site-footer .container_sidebar_three li {
  margin: 0 0 10px;
  padding: 0 0 10px;
  border-bottom: 1px solid #e2e0e0;
  font-size: 14.4px;
}

footer.site-footer .container_sidebar_two li a,
footer.site-footer .container_sidebar_three li a {
  font-size: 14.4px;
}

footer.site-footer .container_sidebar_two .widget_categories,
footer.site-footer .container_sidebar_two .widget_tag_cloud,
footer.site-footer .container_sidebar_two .widget_archive,
footer.site-footer .container_sidebar_three .widget_categories,
footer.site-footer .container_sidebar_three .widget_tag_cloud,
footer.site-footer .container_sidebar_three .widget_archive {
  padding: 10px;
  border: 1px solid #e2e0e0;
  background-color: #ffffff;
}

footer.site-footer .container_sidebar_two .widget_categories,
footer.site-footer .container_sidebar_three .widget_categories {
  margin-bottom: 20px;
}

footer.site-footer .container_sidebar_two .tagcloud a,
footer.site-footer .container_sidebar_three .tagcloud a {
  float: left;
  margin: 0 4px 4px 0;
  padding: 5px 12px;
  font-size: 14.4px;
  text-transform: uppercase;
  background: #212121;
  color: #eeeded;
  transition: background-color 0.4s ease, color 0.4s ease;
}

footer.site-footer .container_sidebar_two .tagcloud a:hover, footer.site-footer .container_sidebar_two .tagcloud a:focus,
footer.site-footer .container_sidebar_three .tagcloud a:hover,
footer.site-footer .container_sidebar_three .tagcloud a:focus {
  background-color: #ff8800;
  color: #ffffff;
}

.comments-area textarea {
  min-height: 100px;
}

.comments-area textarea,
.comments-area input {
  outline: none;
  box-sizing: border-box;
  width: 100%;
  max-width: 641px;
  padding: 10px;
  background-color: #eeeded;
  border: 1px solid #e2e0e0;
}

.comments-area label {
  display: block;
  font-size: 12.8px;
  text-transform: uppercase;
}

.comments-area label span {
  vertical-align: top;
  color: #dc0413;
}

.comments-area input[type="submit"] {
  background-color: #ff8800;
}

.comments-area input[type="submit"]:hover, .comments-area input[type="submit"]:active, .comments-area input[type="submit"]:focus {
  background-color: #000000;
  color: #ffffff;
}

.comments-area .dg_grid_row {
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #ffffff;
  border: 1px solid #e2e0e0;
}

.comments-area .dg_grid_row:last-of-type {
  margin-top: 20px;
}

.comments-area .post_comments h6 {
  margin: 0;
  font-size: 16px;
  text-transform: uppercase;
}

.comments-area .comment_list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.comments-area .comment_list li {
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #e2e0e0;
}

.comments-area .comment_reply_title {
  font-size: 16px;
  text-transform: uppercase;
}

.comments-area .comment-notes,
.comments-area .comment-notes span {
  font-size: 12.8px;
}

.comments-area .form-submit {
  margin-bottom: 2px;
}

